  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  color: var(--text);
  font-family: var(--font-family);
}

h1,
h2,
h3,
h4 {
  margin-bottom: 2rem;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 2rem;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

a {
  color: var(--text);
  text-decoration: none;
}

strong {
  color: var(--accent);
}

:root {
  --primary: #2ecc71;
  --dark: #27ae60;
  --accent: #2c3e50;
  --text: #333;
  --divider: #ecf0f1;
  --dark-grey: #324355;
  --error: #e74c3c;
  --hint: #95a5a6;
  --font-family: "Muli", sans-serif;
  --box-shadow: 0 1px 2px 0 rgba(60, 64, 0.3), 0 1px 3px 1px rgba(60, 64, 67, 0.15);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.title-underline {
  padding-bottom: 2rem;
  border-bottom: 1px solid var(--divider);
}

.btn {
  border: 0;
  border-radius: 3px;
  padding: 1rem 2rem;
  font-weight: 700;
  cursor: pointer;
  transition: box-shadow 0.2s;
}
.btn:hover {
  box-shadow: var(--box-shadow);
  transition: box-shadow 0.2s;
}
.btn-primary {
  background: var(--primary);
  color: white;
}
.btn-secondary {
  border: 1px solid var(--primary);
  background: white;
  color: var(--primary);
}
.btn-danger {
  border: 1px solid var(--error);
  background: white;
  color: var(--error);
}

.text-error {
  color: var(--error);
}

.calc {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.calc .modal {
  background: white;
  min-width: 30rem;
  border-radius: 3px;
  padding: 5rem;
  box-shadow: var(--box-shadow);
  text-align: center;
}
.calc .modal p {
  margin: 1rem 0 3rem 0;
}
.calc .modal .btn {
  margin-left: 0.5rem;
}

/* Landscape phones and down */
/* Landscape phone to portrait tablet */
/* Portrait tablet to landscape and desktop */
/* Large desktop */
.container {
  min-height: 100vh;
  display: grid;
  grid: "header" auto "content" 1fr "footer" auto/auto;
}

header {
  grid-area: header;
  background: var(--dark);
  padding: 2rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
header a {
  color: white;
}
header .header-brand {
  font-size: 4rem;
  font-weight: 700;
}
header .header-menu {
  position: relative;
}
header ul {
  display: flex;
}
@media (max-width: 480px) {
  header ul {
    display: none;
  }
}
header ul li .header-nav {
  font-size: 1.8rem;
  padding: 0 10px;
}
header ul li .active {
  font-weight: 700;
  text-decoration: underline;
}
header .header-menu-icon {
  display: none;
  font-size: 3rem;
  color: white;
}
@media (max-width: 480px) {
  header .header-menu-icon {
    display: block;
  }
}
header .mobile-menu {
  display: none;
  position: absolute;
  box-shadow: var(--box-shadow);
  top: 9.5rem;
  right: 1rem;
  padding: 3rem 1.5rem;
  width: 20rem;
  background: white;
}
header .mobile-menu ul {
  display: block;
}
header .mobile-menu li {
  margin: 2rem 0;
}
header .mobile-menu a {
  color: var(--text);
}
header .mobile-menu.open {
  display: block;
}

.content {
  grid-area: content;
  padding: 2rem;
  background: var(--divider);
}

footer {
  grid-area: footer;
  padding: 2rem;
  background: var(--dark-grey);
  font-size: 1.8rem;
  text-align: center;
  color: white;
